
        @import "@/assets/scss/All.scss";
        





































body {
  width: 100vw;
}
.me {
  padding-top: 60px;
  margin-bottom: 40px;

  img {
    width: 100px;
    height: auto;
    border: 1px solid #eee;
    border-radius: 50%;
  }
  dl {
    dt {
      font-size: 1.2em;
      line-height: 1.6em;
      margin-top: 10px;
    }
    dd {
      font-size: 0.8em;
      line-height: 1.6em;
    }
  }
}
.reservation {
  dl {
    width: 300px;
    margin: auto;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    text-align: center;
    border: 1px solid #eee;
    padding: 20px;
    border-radius: 20px;
    dt {
      width: 100%;
      font-weight: bold;
      color: #cf5724;
      font-size: 1.4em;
      line-height: 1.6em;
    }
    dd {
      width: 50%;
      font-size: 0.8em;
      line-height: 1.6em;
    }
    dd:last-child {
      width: 100%;
      display: flex;
      flex-flow: row;
      p {
        width: 50%;
      }
      span {
        font-size: 1.2em;
        line-height: 2em;
        margin: 0 10px;
        color: #cf5724;
        font-weight: bold;
      }
    }
  }
}
